import React from 'react';
import '../../style/general.less';
import fetch from '../../fetch/axios';
import QAModule from '../chat/qaModoule';

class HistoryViewer extends React.Component {
  state = {
    sId: "", // GUID
    historyData: [], // 
    iGetting: false, // 
  }
  componentDidMount() {
    // console.log("===>>", this.props.sId)
    this.setState({ sId: this.props.sId }, () => {
      this.getHistory();
    })
  }
  componentDidUpdate(prevProps) {
    if (this.props.sId !== prevProps.sId) {
      // console.log("===>>", this.props.sId)
      this.setState({ sId: this.props.sId }, () => {
        this.getHistory();
      })
    }
  }

  getHistory() {
    this.setState({ historyData: [], iGetting: true })

    fetch.get(`/api/chat/conversations/${this.state.sId}`).then(res => {
      if (res && res.success) {
        let historyData = res.data
        this.setState({ historyData, iGetting: false });
      }
    }).catch(err => {
      this.setState({ historyData: [], iGetting: false });
    })
  }

  render() {
    return (
      <div className='history_viewer'>
        {/*  */}
        <div className='history_viewer_main'>
          {this.state.historyData.map((item, index) => {
            return (
              <QAModule
                key={index}
                data={item}
                index={index}
                isHistory={true}
                {...this.props}
                // search={(q, n)=>{ this.search(q, n) }}
                // getForm={(fData)=>{ this.getForm(fData) }}
                // viewFile={(e)=>{ this.viewFile(e) }}
                // scrollToBottom={()=>{ this.scrollToBottom() }}
                // selectGuider={(id)=>{ this.setState({guideId: id}) }}
              ></QAModule>
            )
          })}
        </div>
      </div>
    )
  }
}

export default HistoryViewer