import React from 'react';
import { Button, Input, Table, Select, Drawer, DatePicker, message } from 'antd';
import { SearchOutlined, CloseOutlined } from '@ant-design/icons';
import '../../style/general.less';
// import PItem from './projectItem';
import fetch from '../../fetch/axios';
import moment from 'moment';
import HistoryViewer from './historyViewer';
import ic_history_msg from '../../images/icon/ic_history_msg.svg';

const { Search } = Input;

class HistoryList extends React.Component {
  state = {
    page: 0,
    pageSize: 10,
    total: 0,
    sort: 0,
    sortKey: "",

    historyList: [],
    botList: [], // bot

    botId: 0, // bot
    chatDate: "", // 
    status: -1,
    search: "",
    totalUsage: 0, // 

    isGetting: false, // 

    isDrawerOpen: false, // 
    subjectGUID: "", // GUID
    sTitle: "", // 
    sDate: "", // 
  }
  componentDidMount() {
    // this.getBotList();
    this.getHistoryList();
  }

  changeSearch(e) {
    this.setState({ search: e.target.value });
  }
  // 
  getHistoryList(query) {
    // let { rights } = this.props.reducer;
    // if (!rights.history) return;

    this.setState({ isGetting: true })

    // console.log( this.state.sortKey, this.state.sort, this.state.page, this.state.pageSize )

    let obj = {
      "pageNumber": this.state.page,
      "pageSize": this.state.pageSize,
      "status": this.state.status,
      "bot_ID": this.state.botId,
      "searchKey": this.state.search,
      "chatDate": this.state.chatDate,
      // "currentUserID": "",
      "ColumnName": this.state.sortKey,
      "ColumnSort": this.state.sort,
    }
    fetch.post(`/api/Chat/conversations/list`, obj).then(res => {
      if (res && res.success) {
        let historyList = res.data;
        this.setState({ historyList, isGetting: false, total: res.totalCount });
        // token
        this.props.setToken?.(res.message);
      } else {
        this.setState({ historyList: [], isGetting: false });
        console.log(res.message);
        message.error(res.message);
      }
    }).catch(err => {
      this.setState({ historyList: [], isGetting: false });
    })
  }
  // bot
  // getBotList() {
  //   // let { rights } = this.props.reducer;
  //   // if (!rights.history) return;

  //   fetch.get(`/api/project`).then(res => {
  //     if (res && res.success) {
  //       let botList = res.data
  //       botList.forEach(element => {
  //         element.label = element.project_name;
  //         element.value = element.id;
  //       });
  //       this.setState({ botList });
  //     }
  //   }).catch(err => {
  //     this.setState({ botList: [] });
  //   })
  // }

  handleCheck(obj) {
    this.setState({
      isDrawerOpen: true,
      subjectGUID: obj.subjectGUID,
      sTitle: obj.title,
      sDate: moment(obj.cDate).format("YYYY-MM-DD  HH:mm"),
    })
  }

  render() {
    const { t } = this.props;

    const columns = [
      {
        title: t('general.Number'), // 
        dataIndex: 'index',
        render: (text, obj, index) => index + 1 + this.state.page * this.state.pageSize,
        width: 100,
        fixed: 'left',
      },
      {
        title: t('general.list.'),
        dataIndex: 'userName',
        width: 200,
        fixed: 'left',
        sorter: true,
      },
      {
        title: t('general.list.'),
        dataIndex: 'title',
        width: 400,
        sorter: true,
      },{
        title: t('general.list.'),
        dataIndex: 'cDate',
        render: (text, obj) => moment(text).format("YYYY-MM-DD  HH:mm") || '-',
        width: 150,
        sorter: true,
      },
      // {
      //   title: t('general.list.bot'),
      //   dataIndex: 'botName',
      //   width: 150,
      //   sorter: true,
      // },
      // {
      //   title: t('general.list.'),
      //   dataIndex: 'islike',
      //   width: 100,
      //   sorter: true,
      // }, {
      //   title: t('general.list.'),
      //   dataIndex: 'unlike',
      //   width: 100,
      //   sorter: true,
      // }, {
      //   title: t('general.list.'),
      //   dataIndex: 'channel',
      //   width: 100,
      //   sorter: true,
      // },
      {
        title: t('general.list.token'),
        dataIndex: 'token',
        width: 150,
        sorter: true,
      }, {
        title: t('general.list.'),
        dataIndex: 'operate',
        render: (text, obj) => <div className='operate_box'>
          <Button size='small' type="text" onClick={() => this.handleCheck(obj)}><span className='text'>{t('general.list.')}</span></Button>
        </div>,
        width: 100,
        fixed: 'right',
      },
    ]

    return (
      <div className='history_list'>

        <div className='hl_content'>
       
          <div className='hl_header'>
            <div className='hl_header_lt'>
              {/*  */}
              {/* <Select
                defaultValue={-1}
                style={{ width: 120 }}
                onChange={(e)=>{
                  this.setState({status: e}, ()=>{
                    this.getHistoryList(true);
                  })
                }}
                size="large"
              >
                <Select.Option value={-1}>{t('general.list.')}</Select.Option>
                <Select.Option value={1}>{t("ON")}</Select.Option>
                <Select.Option value={0}>{t("OFF")}</Select.Option>
              </Select> */}
              {/* Bot */}
              {/* <Select
                showSearch
                placeholder={t('general.list.Bot')}
                optionFilterProp="children"
                onChange={(e) => {
                  this.setState({ botId: e||0, page: 0 }, () => {
                    this.getHistoryList();
                  })
                 }}
                onSearch={()=>{
                  // this.getBotList();
                }}
                filterOption={(input, option) =>
                  (option?.label ?? '').toLowerCase().includes(input.toLowerCase())
                }
                options={this.state.botList}
                style={{width: 200}}
                allowClear
              /> */}
              {/*  */}
              <DatePicker
                onChange={(date, dateString) => {
                  this.setState({ chatDate: dateString, page: 0 }, () => {
                    this.getHistoryList();
                  })
                }}
                // , marginLeft: '1rem'
                style={{width: 200}}
                placeholder={t('general.list.')}
              />
            </div>
            <div className='hl_header_gt'>
              <Search
                value={this.state.search}
                placeholder={t('general.list.')}
                onChange={(e) => { this.changeSearch(e) }}
                onSearch={() => { this.getHistoryList() }}
                className='sx_search'
                enterButton={
                  <div className='search_btn'>
                    <SearchOutlined />
                    {t('general.list.')}
                  </div>
                }
                size="large"
                style={{ width: 400 }}
                allowClear
              />
            </div>
          </div>
          <Table
            columns={columns}
            dataSource={this.state.historyList}
            loading={this.state.isGetting}
            onChange={(pagination, filters, sorter) => {
              // console.log(pagination, filters, sorter);
              if (this.state.isGetting) return;
              
              this.setState({
                page: pagination.current - 1,
                pageSize: pagination.pageSize,
                sort: sorter.order === 'descend' ? 1 : 0,
                sortKey: sorter.column?.dataIndex || "",
                selectedRowKeys: []
              }, () => {
                this.getHistoryList();
              })
            }}
            pagination={
              {
                total: this.state.total,
                pageSize: this.state.pageSize,
                current: this.state.page + 1,
                showSizeChanger: true,
                showQuickJumper: true,
                showTotal: (total, range) => `${range[0]}-${range[1]} of ${total} items`,
              }
            }
            rowKey={(row) => row.id}
            // rowSelection={{
            //   selectedRowKeys: this.state.selectedRowKeys,
            //   onChange: (selectedRowKeys, selectedRows) => {
            //     this.setState({ selectedRowKeys })
            //   },
            // }}
            scroll={{ x: 1000 }}
          />
        </div>
        <Drawer
          title={
            <div style={{display: 'flex', justifyContent: 'space-between', alignItems: 'center'}}>
              <div style={{display: 'flex', alignItems: 'center'}}>
                <img src={ic_history_msg} alt=""/>
                <div style={{fontSize: '20px', margin: '0 1rem 0 0.375rem'}}>
                  <div style={{width: '500px', maxWidth: 'calc(100vw - 210px)', whiteSpace: 'nowrap', overflow: 'hidden', textOverflow: 'ellipsis'}}>
                    {this.state.sTitle||t('general.list.')}
                  </div>
                </div>
                <span style={{fontWeight: '400'}}>{this.state.sDate}</span>
              </div>
              <Button type="text" icon={<CloseOutlined />} onClick={() => { this.setState({ isDrawerOpen: false }) }}></Button>
            </div>
          }
          placement="right"
          closable={false}
          onClose={() => { this.setState({ isDrawerOpen: false }) }}
          open={this.state.isDrawerOpen}
          width={1024}
        >
          <HistoryViewer sId={this.state.subjectGUID} {...this.props}></HistoryViewer>
        </Drawer>
      </div>
    )
  }
}

export default HistoryList